.banner {
    display: block;
    position: relative;
    color: var(--text-color-primary);
    width: 100%;
    height: 100vh;
    padding: 0 var(--page-pad-small);
    padding-top: 120px;
    background-image: url('../assets/images/building.jpg');
    background-size: contain;
    background-position: center bottom;
    background-repeat: no-repeat;
    
    display: flex;
    flex-wrap: wrap;
}

.intro, .hero {
    position: relative;
    overflow: hidden;
    width: 100%;
    max-width: 100%;
    height: 50%;
}
.intro {
    display: flex;
}
.intro-greet {
    width: 100%;
    -ms-flex-item-align: center;
    -ms-grid-row-align: center;
    align-self: center;
}
.intro .text-brand {
    color: var(--text-color-brand);
}
.intro .title {
    font-size: var(--text-title-size-small);
    line-height: var(--text-title-lineheight-size-small);
    font-weight: 900;
}
.intro .role {
    font-size: var(--text-subtitle-size-small);
    font-weight: 100;
    margin: 8px 0;
}
.intro .bio {
    font-size: var(--text-p-size-large);
}
.btn-about {
    max-width: 150px;
}

.hero-image {
    background-image: url('../assets/images/self.png');
    background-size: contain;
    background-position: center bottom;
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
}
.hero-image img {
    width: 80%;
}

@media only screen and (min-width: 48em) {
    .intro {
        max-width: 40%;
    }
    .hero {
        max-width: 60%;
    }
    .intro, .hero {
        height: 100%;
    }
    .banner {
        padding: 0 var(--page-pad-medium);
    }
    .intro .title {
        font-size: var(--text-title-size-medium);
        line-height: var(--text-title-lineheight-size-medium);
        font-weight: 900;
    }
    .intro .role {
        font-size: var(--text-subtitle-size-medium);
        font-weight: 100;
        margin: 8px 0;
    }
    .intro .bio {
        font-size: var(--text-p-size-medium);
    }
    .hero-image {
        background-position: right bottom;
    }
}

@media only screen and (min-width: 78rem) {
    .banner {
        padding: 0 var(--page-pad-large);
    }
    .intro .title {
        font-size: var(--text-title-size-large);
        line-height: var(--text-title-lineheight-size-large);
        font-weight: 900;
    }
    .intro .role {
        font-size: var(--text-subtitle-size-large);
        font-weight: 100;
        margin: 8px 0;
    }
    .intro .bio {
        font-size: var(--text-p-size-large);
    }
}